import React, { useEffect } from 'react';
import { connect } from "react-redux"

import { useGetCheckoutSessionMutation } from '../../auth/slices/authApiSlice';
import Loading from '../../../components/Loading';
import 'bootstrap/dist/css/bootstrap.min.css';

const mapStateToProps = (state) => ({
    user: state.step
});

const Pricing = ({ user }) => {
    const [getCheckoutSession, { isLoading }] = useGetCheckoutSessionMutation()
    useEffect(() => {
        if (user.email) {
            getCheckoutSession({email:user.email, first_name:user.firstName, last_name:user.lastName}).unwrap().then(function (res) {
                window.location.href = res.url
            })
        }

    }, [user.email])



    return (
        <Loading />
    )
}

export default connect(mapStateToProps)(Pricing);
